import {
  Avatar,
  Badge,
  TableBody,
  TableCell,
  TableRow,
} from "@windmill/react-ui";
import MainDrawer from "components/drawer/MainDrawer";
import ProductDrawer from "components/drawer/ProductDrawer";
import CheckBox from "components/form/CheckBox";
import DeleteModal from "components/modal/DeleteModal";
import EditDeleteButton from "components/table/EditDeleteButton";
import ShowHideButton from "components/table/ShowHideButton";
import defalutimage from "../../assets/img/forgot-password-office-dark.jpeg";
import Tooltip from "components/tooltip/Tooltip";
import useToggleDrawer from "hooks/useToggleDrawer";
import { t } from "i18next";
import { useState } from "react";
import { FiZoomIn } from "react-icons/fi";
import { Link } from "react-router-dom";
import { showingTranslateValue } from "utils/translate";
import { useContext } from "react";

//internal import

const ProductTable = ({
  addOnList,
  isCheck,
  setIsCheck,
  getAddOnList,
  lang,
  handleUpdate,
  wholeCount,
}) => {
  const { title, serviceId, handleModalOpen } = useToggleDrawer();

  const handleClick = (e) => {
    const { id, checked } = e.target;
    console.log("id", id, checked);

    setIsCheck([...isCheck, id]);
    if (!checked) {
      setIsCheck(isCheck.filter((item) => item !== id));
    }
  };

  return (
    <>
      {isCheck?.length < 1 && (
        <DeleteModal getAddOnList={getAddOnList} id={serviceId} title={title} />
      )}

      <TableBody>
        {addOnList?.map((properties, i) => (
          <TableRow key={i + 1}>
            <TableCell>
              <span className="text-sm">
                {(wholeCount?.currentPage - 1) * wholeCount?.pageSize + i + 1}
              </span>
            </TableCell>
            <TableCell className="textcorrect">
              <span className="text-sm ">{properties?.User?.name}</span>
            </TableCell>
            <TableCell className="textcorrect">
              <span className="text-sm">{properties?.category}</span>
            </TableCell>
            <TableCell className="textcorrect">
              <span className="text-sm">
                {properties?.Add_on_bill_ala_cartes?.length > 0 && (
                  <>
                    {" "}
                    ala-carte:-
                    {properties?.Add_on_bill_ala_cartes?.map(
                      (item) => item?.name + ", "
                    )}
                    <br />
                  </>
                )}
                {properties?.Add_on_bill_activities?.length > 0 && (
                  <>
                    {" "}
                    Activities:-
                    {properties?.Add_on_bill_activities?.map(
                      (item) => item?.name + ", "
                    )}
                    <br />
                  </>
                )}
                <>Others:-{properties?.others_description}</>
              </span>
            </TableCell>
            {/* <TableCell>
                {product.stock > 0 ? (
                  <Badge type="success">{t("Selling")}</Badge>
                ) : (
                  <Badge type="danger">{t("SoldOut")}</Badge>
                )}
              </TableCell> */}
            <TableCell>
              <span className="text-sm">{`₹${properties?.total_amount}`}</span>
            </TableCell>
            {/* <TableCell>
                <Link
                  to={`/product/${product._id}`}
                  className="flex justify-center text-gray-400 hover:text-green-600"
                >
                  <Tooltip
                    id="view"
                    Icon={FiZoomIn}
                    title={t("DetailsTbl")}
                    bgColor="#10B981"
                  />
                </Link>
              </TableCell> */}
            {/* <TableCell className="text-center">
                <ShowHideButton id={properties._id} status={properties.status} />
              </TableCell> */}
            {/* <TableCell>
              <EditDeleteButton
                id={properties.id}
                product={properties}
                isCheck={isCheck}
                handleUpdate={handleUpdate}
                handleModalOpen={handleModalOpen}
                title={showingTranslateValue(properties?.title, lang)}
              />
            </TableCell> */}
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default ProductTable;
