import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Select,
  Input,
  Button,
  Card,
  CardBody,
  Pagination,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import useAsync from "hooks/useAsync";
import useToggleDrawer from "hooks/useToggleDrawer";
import UploadManyTwo from "components/common/UploadManyTwo";
import NotFound from "components/table/NotFound";
import ProductServices from "services/ProductServices";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import propertiesformlocal from "../../utils/AddonBills";
import ProductTable from "components/AddOnBills/ListAddOnBills";
import SelectCategory from "components/form/SelectCategory";
import MainDrawer from "components/drawer/MainDrawer";
import ProductDrawer from "components/AddOnBills/CreateAddOnBills";
import CheckBox from "components/form/CheckBox";
import useProductFilter from "hooks/useProductFilter";
import { FiEdit, FiTrash2 } from "react-icons/fi";
import DeleteModal from "components/modal/DeleteModal";
import BulkActionDrawer from "components/drawer/BulkActionDrawer";
import TableLoading from "components/preloader/TableLoading";
import SettingServices from "services/SettingServices";
import { AddOnLists } from "services/Api";

const Products = () => {
  const {
    title,
    allId,
    serviceId,
    handleDeleteMany,
    handleUpdate,
    handleUpdateMany,
  } = useToggleDrawer();

  console.log(serviceId, "serviceId");

  const { t } = useTranslation();
  const {
    toggleDrawer,
    lang,
    currentPage,
    // handleChangePage,
    searchText,
    category,
    setCategory,
    searchRef,
    handleSubmitForAll,
    sortedField,
    setSortedField,
    limitData,
    isUpdated,
  } = useContext(SidebarContext);

  const { data, loading } = useAsync(() =>
    ProductServices.getAllProducts({
      page: currentPage,
      limit: limitData,
      category: category,
      title: searchText,
      price: sortedField,
    })
  );

  const { data: globalSetting } = useAsync(SettingServices.getGlobalSetting);
  const currency = globalSetting?.default_currency || "$";
  // console.log("product page", data);
  const [wholeCount, setwholeCount] = useState({});
  // react hooks
  const [isCheckAll, setIsCheckAll] = useState(false);
  const [isCheck, setIsCheck] = useState([]);
  const [addOnList, setAddOnList] = useState([]);
  const [loadings, setLoading] = useState(false);
  const [pagenumber, setpagenumber] = useState(1);

  const handleSelectAll = () => {
    setIsCheckAll(!isCheckAll);
    setIsCheck(propertiesformlocal.map((li) => li._id));
    if (isCheckAll) {
      setIsCheck([]);
    }
  };

  const getAddOnList = async (pagenumber, search = "") => {
    const addOn = await AddOnLists(pagenumber, search);
    setLoading(false);
    setAddOnList(addOn?.data?.data);
    setwholeCount(addOn?.data?.data?.pagination);
  };

  useEffect(() => {
    getAddOnList(pagenumber);
  }, []);

  const {
    serviceData,
    filename,
    isDisabled,
    handleSelectFile,
    handleUploadMultiple,
    handleRemoveSelectFile,
  } = useProductFilter(propertiesformlocal);

  const handleChangePage = (e) => {
    setpagenumber(e);
    // setLoading(true);
    getAddOnList(e);
  };

  const handleSearch = (search) => {
    getAddOnList(pagenumber, search);
  };

  return (
    <>
      <PageTitle>{t("Add On Bills")}</PageTitle>
      {/* <MainDrawer>
        <ProductDrawer id={serviceId} getAddOnList={getAddOnList} />
      </MainDrawer> */}
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            {/* <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Button
                  onClick={toggleDrawer}
                  className="w-full rounded-md h-12"
                >
                  <span className="mr-2">
                    <FiPlus />
                  </span>
                  {t("Add Add On Bills")}
                </Button>
              </div>
            </div> */}
          </form>
        </CardBody>
      </Card>

      {loadings ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : addOnList.add_on_list?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell>{t("S.No")}</TableCell>
                <TableCell>{t("Name")}</TableCell>
                <TableCell>{t("Category")}</TableCell>
                <TableCell>{t("Items")}</TableCell>
                <TableCell className="">{t("Amount")}</TableCell>
                {/* <TableCell className="text-right">{t("Action")}</TableCell> */}
                {/* {
                  <TableCell className="text-center">
                  {t("PublishedTbl")}
                </TableCell>
                <TableCell className="text-right">{t("ActionsTbl")}</TableCell>
                } */}
              </tr>
            </TableHeader>
            <ProductTable
              handleUpdate={handleUpdate}
              lang={lang}
              isCheck={isCheck}
              addOnList={addOnList.add_on_list}
              setIsCheck={setIsCheck}
              wholeCount={wholeCount}
              getAddOnList={getAddOnList}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={addOnList?.pagination?.total}
              resultsPerPage={addOnList?.pagination?.pageSize}
              onChange={(e) => {
                handleChangePage(e);
              }}
              label="Add On Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Product" />
      )}
    </>
  );
};

export default Products;
