import { Button } from "@windmill/react-ui";
import Multiselect from "multiselect-react-dropdown";
import React, { useContext, useEffect, useState } from "react";
import { Scrollbars } from "react-custom-scrollbars-2";
import { Modal } from "react-responsive-modal";
import "react-responsive-modal/styles.css";
import { useTranslation } from "react-i18next";
import { FiMinusCircle, FiPlusCircle, FiX } from "react-icons/fi";
import useProductSubmit from "hooks/useAddOnSubmit";
import InputAreaTwo from "components/form/InputAreaTwo";
import UploaderThree from "components/image-uploader/UploaderThree";
import Title from "components/form/Title";
import LabelArea from "components/form/LabelArea";
import Error from "components/form/Error";
import DrawerButton from "components/form/DrawerButton";
import { addOnById, getAddOnFoodAndActivity, getAddOnUser } from "services/Api";
import Price from "components/common/Price";
import { SidebarContext } from "context/SidebarContext";
import spinnerLoadingImage from "../../assets/img/spinner.gif";
//internal import
const ProductDrawer = ({
  id,
  getAllBookingList,
  pagenumber,
  searchname,
  StartDate,
  EndDate,
  expo,
  bookingUser,
  bookingId,
}) => {
  const { t } = useTranslation();
  const [guestsList, setGuestsList] = useState([]);
  const [category, setCategory] = useState([]);
  const [user, setUser] = useState([]);
  const [foodList, setFoodList] = useState([]);
  const [activity, setActivity] = useState([]);
  const [selectFood, setSelectFood] = useState([]);
  const [selectActivity, setSelectActivity] = useState([]);
  const [foodCount, setFoodCount] = useState([]);
  const [actvityCount, setActivityCount] = useState([]);
  const [clicked, setClicked] = useState(false);
  const { toggleDrawer, isDrawerOpen } = useContext(SidebarContext);

  console.log(id, "idd");

  const {
    tag,
    setTag,
    values,
    language,
    register,
    onSubmit,
    errors,
    slug,
    openModal,
    attribue,
    setValues,
    variants,
    imageUrl,
    setImageUrl,
    handleSubmit,
    isCombination,
    variantTitle,
    attributes,
    attTitle,
    handleAddAtt,
    // productId,
    onCloseModal,
    isBulkUpdate,
    globalSetting,
    isSubmitting,
    tapValue,
    setTapValue,
    resetRefTwo,
    handleSkuBarcode,
    handleProductTap,
    // selectedCategory,
    // setSelectedCategory,
    setDefaultCategory,
    defaultCategory,
    setDefaultCategory1,
    defaultCategory1,
    setDefaultCategory2,
    defaultCategory2,
    handleProductSlug,
    handleSelectLanguage,
    handleIsCombination,
    handleEditVariant,
    handleRemoveVariant,
    handleClearVariant,
    handleQuantityPrice,
    handleSelectImage,

    setValue,
  } = useProductSubmit(
    id,
    user,
    foodCount,
    // selectActivity,
    category,
    setUser,
    setFoodCount,
    setSelectFood,
    setSelectActivity,
    setCategory,
    // getAddOnList,
    getAllBookingList,
    pagenumber,
    searchname,
    StartDate,
    EndDate,
    expo,
    actvityCount,
    setActivityCount,
    bookingId
  );

  const CategoriesList = [
    { name: "activity", id: 1 },
    { name: "ala_carte", id: 2 },
    { name: "others", id: 2 },
  ];

  const getAllUser = async () => {
    const res = await getAddOnUser();
    console.log(res, "resres");
    setGuestsList(res?.data?.data?.guest_list);
  };

  console.log(foodCount, "category");

  useEffect(() => {
    let baseprice = 0;
    let cgst = 0;
    let sgst = 0;
    if (category[0]?.name === "ala_carte") {
      foodCount.map((item) => {
        baseprice = baseprice + item?.qty * item?.price;
        cgst = cgst + item?.qty * item.cgst;
        sgst = sgst + item?.qty * item.sgst;
      });
    }
    if (category[0]?.name === "activity") {
      actvityCount.map((item) => {
        baseprice = baseprice + item?.qty * item?.price;
        cgst = cgst + item?.qty * item.cgst;
        sgst = sgst + item?.qty * item.sgst;
      });
    }
    if (category[0]?.name === "other") {
      activity.map((item) => {
        baseprice = baseprice;
        cgst = 0;
        sgst = 0;
      });
    }
    // setValue("base_price", base_price);
    setValue("CGST", cgst);
    setValue("SGST", sgst);
    setValue("total", baseprice + cgst + sgst);
  }, [foodCount, selectActivity, actvityCount]);

  const getActivityFood = async (category, id) => {
    const res = await getAddOnFoodAndActivity(category, id);
    if (category === "activity") {
      setActivity(res?.data?.data?.data_list);
    }
    if (category === "ala_carte") {
      setFoodList(res?.data?.data?.data_list);
    }
    console.log(res, "resres");
  };

  const handleIncrement = (item) => {
    const updatedItems = foodCount?.map((food) =>
      food.id === item.id ? { ...food, qty: food.qty + 1 } : food
    );
    setFoodCount(updatedItems);
  };

  const handleDecrement = (item) => {
    if (item.qty > 1) {
      const updatedItems = foodCount?.map((food) =>
        food.id === item.id ? { ...food, qty: food.qty - 1 } : food
      );
      setFoodCount(updatedItems);
    }
  };

  const handleActivityIncrement = (item) => {
    const updatedItems = actvityCount?.map((food) =>
      food.id === item.id ? { ...food, qty: food.qty + 1 } : food
    );
    setActivityCount(updatedItems);
  };

  const handleActivityDecrement = (item) => {
    if (item.qty > 1) {
      const updatedItems = actvityCount?.map((food) =>
        food.id === item.id ? { ...food, qty: food.qty - 1 } : food
      );
      setActivityCount(updatedItems);
    }
  };

  const getAddOnById = async (id) => {
    const res = await addOnById(id);
    const data = res?.data?.data?.Add_on_bill;
    console.log(data, "res1");
    setUser([data?.User]);
    setCategory(CategoriesList?.filter((item) => item.name === data?.category));
    setSelectFood(
      data?.Add_on_bill_ala_cartes?.map((item) => {
        return {
          ...item,
          id: item?.ala_carte_id,
        };
      })
    );
    setFoodCount(
      data?.Add_on_bill_ala_cartes.map((item) => {
        return {
          ...item,
          id: item?.ala_carte_id,
        };
      })
    );
    setSelectActivity(
      data?.Add_on_bill_activities?.map((item) => {
        return {
          ...item,
          id: item?.activity_id,
        };
      })
    );
    setActivityCount(
      data?.Add_on_bill_activities.map((item) => {
        return {
          ...item,
          id: item?.activity_id,
        };
      })
    );
    setValue("others", data?.others_description);
    setValue("base_price", data?.base_price);
  };

  console.log(selectActivity, selectFood, "selectFoodselectFood");

  useEffect(() => {
    if (user?.length > 0 && category?.length > 0) {
      getActivityFood(category[0]?.name, user[0]?.id);
    }
  }, [user, category]);

  useEffect(() => {
    getAllUser();
    getActivityFood();
  }, []);

  useEffect(() => {
    if (id) {
      getAddOnById(id);
    }
  }, [id]);

  console.log(!!id);

  useEffect(() => {
    if (!id && bookingUser) {
      setUser([bookingUser]);
    }
  }, [bookingUser, id]);

  useEffect(() => {
    setClicked(false);
  }, []);

  useEffect(() => {
    setFoodCount(
      selectFood.map((item) => {
        return {
          ...item,
          qty: 1,
        };
      })
    );
  }, [selectFood]);

  useEffect(() => {
    if (selectActivity?.length > 0) {
      setActivityCount(
        selectActivity?.map((item) => {
          return {
            ...item,
            qty: 1,
          };
        })
      );
    }
  }, [selectActivity]);

  console.log(user, "useruser");
  const dkdk = () => {
    setUser([]);
    setCategory([]);
    setSelectActivity([]);
    setSelectFood([]);
    setActivityCount([]);
    setClicked(false);
    setValue("others", "");
    setValue("Total_amount", "");
  };
  return (
    <>
      <Modal
        open={openModal}
        onClose={() => {
          onCloseModal();
        }}
        center
        closeIcon={
          <div className="absolute top-0 right-0 text-red-500 focus:outline-none active:outline-none text-xl border-0">
            <FiX className="text-3xl" />
          </div>
        }
      >
        <div className="cursor-pointer">
          <UploaderThree
            imageUrl={imageUrl}
            setImageUrl={setImageUrl}
            handleSelectImage={handleSelectImage}
          />
        </div>
      </Modal>

      <div className="w-full relative p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300">
        {id ? (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Edit Add-On-Bill")}
          />
        ) : (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Add Add On Bills")}
          />
        )}
      </div>

      <Scrollbars className="track-horizontal thumb-horizontal w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <form onSubmit={handleSubmit(onSubmit)} className="block" id="block">
          {tapValue === "Basic Info" && (
            <div className="px-6 pt-8 flex-grow w-full h-full max-h-full pb-40 md:pb-32 lg:pb-32 xl:pb-32">
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Select Guests")} />
                <div className="col-span-8 sm:col-span-4">
                  <Multiselect
                    displayValue="name"
                    isObject={true}
                    singleSelect={true}
                    ref={resetRefTwo}
                    hidePlaceholder={true}
                    onKeyPressFn={function noRefCheck() {}}
                    onRemove={(v) => setUser(v)}
                    onSearch={function noRefCheck() {}}
                    onSelect={(v) => setUser(v)}
                    selectedValues={user}
                    options={guestsList}
                    placeholder={"Select Guests"}
                    disable={true}
                  />
                  {user.length < 1 && clicked && (
                    <span className="text-red-400 text-sm mt-2">
                      {"Guest name required"}
                    </span>
                  )}
                </div>
              </div>
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Select Category")} />
                <div className="col-span-8 sm:col-span-4">
                  <Multiselect
                    displayValue="name"
                    isObject={true}
                    singleSelect={true}
                    ref={resetRefTwo}
                    hidePlaceholder={true}
                    onKeyPressFn={function noRefCheck() {}}
                    onRemove={() => setCategory([])}
                    onSearch={function noRefCheck() {}}
                    onSelect={(v) => setCategory(v)}
                    selectedValues={category}
                    options={CategoriesList}
                    placeholder={"Select Category"}
                  ></Multiselect>
                  {category.length < 1 && clicked && (
                    <span className="text-red-400 text-sm mt-2">
                      {"Category required"}
                    </span>
                  )}
                </div>
              </div>
              {category[0]?.name === "activity" && (
                <>
                  {" "}
                  <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                    <LabelArea label={t("Select Activities")} />
                    <div className="col-span-8 sm:col-span-4">
                      <Multiselect
                        displayValue="name"
                        isObject={true}
                        singleSelect={false}
                        hidePlaceholder={true}
                        onKeyPressFn={function noRefCheck() {}}
                        onRemove={(v) => setSelectActivity(v)}
                        onSearch={function noRefCheck() {}}
                        onSelect={(v) => setSelectActivity(v)}
                        selectedValues={selectActivity}
                        options={activity}
                        placeholder={"Select Activities"}
                      ></Multiselect>
                    </div>
                  </div>
                  {selectActivity?.length > 0 && (
                    <>
                      {actvityCount?.map((item) => (
                        <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                          <LabelArea label={item?.name} />
                          <div
                            style={{
                              alignItems: "center",
                              display: "flex",
                              flexDirection: "row",
                              gap: "4px",
                            }}
                          >
                            <FiMinusCircle
                              style={{ cursor: "pointer" }}
                              onClick={() => handleActivityDecrement(item)}
                            />
                            {item?.qty}
                            <FiPlusCircle
                              style={{ cursor: "pointer" }}
                              onClick={() => handleActivityIncrement(item)}
                            />
                          </div>
                        </div>
                      ))}
                    </>
                  )}
                </>
              )}
              {category[0]?.name === "ala_carte" && (
                <>
                  <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                    <LabelArea label={t("Select Ala-carte")} />
                    <div className="col-span-8 sm:col-span-4">
                      <Multiselect
                        displayValue="name"
                        isObject={true}
                        singleSelect={false}
                        ref={resetRefTwo}
                        hidePlaceholder={true}
                        onKeyPressFn={function noRefCheck() {}}
                        onRemove={(v) => setSelectFood(v)}
                        onSearch={function noRefCheck() {}}
                        onSelect={(v) => setSelectFood(v)}
                        selectedValues={selectFood}
                        options={foodList}
                        placeholder={"Select Ala-carte"}
                      ></Multiselect>
                    </div>
                  </div>
                  {selectFood?.length > 0 && (
                    <>
                      {foodCount?.map((item) => (
                        <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                          <LabelArea label={item?.name} />
                          <div
                            style={{
                              alignItems: "center",
                              display: "flex",
                              flexDirection: "row",
                              gap: "4px",
                            }}
                          >
                            <FiMinusCircle
                              style={{ cursor: "pointer" }}
                              onClick={() => handleDecrement(item)}
                            />
                            {item?.qty}
                            <FiPlusCircle
                              style={{ cursor: "pointer" }}
                              onClick={() => handleIncrement(item)}
                            />
                          </div>
                        </div>
                      ))}
                    </>
                  )}
                </>
              )}
              {category[0]?.name === "others" && (
                <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label={t("Other")} />
                  <div className="col-span-8 sm:col-span-4">
                    <InputAreaTwo
                      register={register}
                      label={t("other")}
                      name="others"
                      type="text"
                      placeholder={t("Others")}
                    />
                  </div>
                </div>
              )}
              {category[0]?.name === "others" && (
                <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label={t("Total amount")} />
                  <div className="col-span-8 sm:col-span-4">
                    <InputAreaTwo
                      register={register}
                      label={t("Total_amount")}
                      disabled={category[0]?.name !== "others"}
                      name="base_price"
                      type="number"
                      placeholder={t("Total amount")}
                    />
                    <Error errorName={errors.base_price} pos />
                  </div>
                </div>
              )}
              {/* <div className="sm:col-span-3">
                <div className="sm:col-span-3">
                  <div className="flex1">
                   <div className="sm:col-span-5 w-230">
                      <InputAreaTwo
                        register={register}
                        label={t("Base price")}
                        disabled={category[0]?.name !== "others"}
                        name="base_price"
                        type="number"
                        placeholder={t("Base price")}
                      />
                      <Error errorName={errors.Base_price} pos />
                    </div> 

                     <div
                      className="sm:col-span-5"
                      style={{ marginTop: "-25px" }}
                    >
                      <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                        {t("CGST")}
                      </label>
                      <div className="sm:col-span-5 ">
                        <InputAreaTwo
                          register={register}
                          disabled={category[0]?.name !== "others"}
                          label={t("CGST")}
                          name="CGST"
                          type="number"
                          placeholder={t("CGST")}
                        />
                        <Error errorName={errors.SGST} pos />
                      </div>
                    </div>
                    <div
                      className="sm:col-span-5 "
                      style={{ marginTop: "-25px" }}
                    >
                      <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                        {t("SGST")}
                      </label>
                      <div className="sm:col-span-5">
                        <InputAreaTwo
                          register={register}
                          disabled={category[0]?.name !== "others"}
                          label={t("SGST")}
                          name="SGST"
                          type="number"
                          placeholder={t("SGST")}
                        />
                        <Error errorName={errors.CGST} pos />
                      </div>
                    </div> 
                    <div
                      className="sm:col-span-5"
                      style={{ marginTop: "-25px" }}
                    >
                      <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                        {t("Total Amount")}
                      </label>
                      <div className="sm:col-span-5 w-230">
                        <InputAreaTwo
                          register={register}
                          label={t("Total_amount")}
                          disabled={category[0]?.name !== "others"}
                          name="total"
                          type="number"
                          placeholder={t("Total amount")}
                        />
                        <Error errorName={errors.total} pos />
                      </div>
                    </div>
                  </div>
                {/* </div> */}
            </div>
            // </div> */}
          )}
          <div
            className="fixed z-10 bottom-0 w-full right-0 py-4 lg:py-8 px-6 grid gap-4 lg:gap-6 xl:gap-6 md:flex xl:flex bg-gray-50 border-t border-gray-100 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300"
            style={{ right: !isDrawerOpen && -50 }}
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Button
                onClick={() => {
                  toggleDrawer();
                  onCloseModal();
                  dkdk();
                }}
                className="h-12 bg-white w-full text-red-500 hover:bg-red-50 hover:border-red-100 hover:text-red-600 dark:bg-gray-700 dark:border-gray-700 dark:text-gray-500 dark:hover:bg-gray-800 dark:hover:text-red-700"
                layout="outline"
              >
                {t("CancelBtn")}
              </Button>
            </div>

            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              {isSubmitting ? (
                <Button disabled={true} type="button" className="w-full h-12">
                  <img
                    src={spinnerLoadingImage}
                    alt="Loading"
                    width={20}
                    height={10}
                  />{" "}
                  <span className="font-serif ml-2 font-light">Processing</span>
                </Button>
              ) : (
                <Button
                  type="submit"
                  disabled={
                    (category[0]?.name === "activity" &&
                      selectActivity?.length === 0) ||
                    (category[0]?.name === "ala_carte" &&
                      selectFood?.length === 0)
                  }
                  className="w-full h-12"
                >
                  <span>{id ? "Edit Bill" : "Add Bill"} </span>
                </Button>
              )}
            </div>
          </div>
          {/* <DrawerButton
            id={id}
            save
            title="Product"
            imageUrl={imageUrl} setImageUrl={setImageUrl} onCloseModal={onCloseModal}
            isSubmitting={isSubmitting}
            handleProductTap={handleProductTap}
          />
          <div
            onClick={() => {
              setClicked(true);
              dkdk()
            }}
          >
            <DrawerButton
              id={id}
              imageUrl={imageUrl} setImageUrl={setImageUrl} onCloseModal={onCloseModal}
              title="Add on Bills"
              isSubmitting={isSubmitting}
            />
          </div> */}
        </form>
      </Scrollbars>
    </>
  );
};

export default React.memo(ProductDrawer);
